@import "functions";
@import "variables";
@import "mixins";
@import "utilities";

.btn-toggle-wrapper {
  a {
    color: $black;
    text-decoration: none;
  }

  &.active {
    font-weight: bold;
  }
  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.btn-toggle {
  display: inline-flex;
  align-items: center;
  padding: .25rem .5rem;
  font-weight: 600;
  color: rgba(0, 0, 0, .65);
  background-color: transparent;
  border: 0;

  &::before {
    width: 1.25em;
    line-height: 0;
    content: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='rgba%280,0,0,.5%29' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M5 14l6-6-6-6'/%3e%3c/svg%3e");
    transition: transform .35s ease;
    transform-origin: .5em 50%;
  }

  &[aria-expanded="true"] {
    color: $black;

    &::before {
      transform: rotate(90deg);
    }
  }
}

.btn-toggle-static::before {
  width: 1.25em;
  line-height: 0;
  content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16" enable-background="new 0 0 16 16" height="16" width="16"><g><circle cx="8" cy="8" r="6" stroke-width="0" fill="rgba%280,0,0,.5%29" /></g></svg>');
  transition: transform .35s ease;
  transform-origin: .5em 50%;
}

.btn-toggle-nav a {
  display: inline-flex;
  padding: .1875rem .5rem;
  margin-top: .125rem;
  margin-left: 1.25rem;
  text-decoration: none;

  &.active{
    font-weight: bold;
  }
  &:hover,
  &focus {
    text-decoration: underline;
  }
}
